@use '../config' as *;

/*==========  SIZE  ==========*/

/* Text size */
html {
	font-size: 16px;
}
body, .text {
	font-size: 1rem;
	line-height: 1.65em;
	font-weight: var(--regular);
}
.text{
	&-md{
		@include font-size(16px, 1.3em);
	}
	&-sm {
		@include font-size(14px, 1.3em);
	}
	&-lg {
		@include font-size(20px, 1.4em);
	}
	&-xl {
		@include font-size(24px, 1.3em);
	}
}

h1, .h1 {
	@include font-size(3rem, 1.2em);
}

h2, .h2 {
	@include font-size(1.8rem, 1.2em);
}

h3, .h3 {
	@include font-size(1.4rem, 1.2em);
}

h4, .h4 {
	@include font-size(1.2rem, 1.2em);
}

h5, .h5 {
	@include font-size(1.1rem, 1.4em);
}

h6, .h6 {
	@include font-size(1rem, 1.4em);
}

/*==========  MEDIA QUERIES  ==========*/

// @media (max-width: 1368px)

// @media (max-width: 1199px){}

@media (max-width: 991px){
	html {
		font-size: 14px;
	}
}

