/*==========  Form Style  ==========*/

input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
	color: #222;
	opacity: 1;
}

input:-moz-placeholder,
textarea:-moz-placeholder {
	color: #222;
	opacity: 1;
}

input::-moz-placeholder,
textarea::-moz-placeholder {
	color: #222;
	opacity: 1;
}

input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
	color: #222;
	opacity: 1;
}

input, textarea, select {
	border-radius: 0;
	background: none;
	border: none;
	margin: 0;
	width: 100%;
	padding: 0;
}

textarea {
	display: block;
	resize: none;
	overflow: auto;
}

select::-ms-expand {
	display: none;
}

.invalid {
	border-color: red;
	color: red;
}

/*==========  Text fields  ==========*/

.input-error{
	display: none;
	margin-top: 5px;
	color: #FA665B;
	font-size: 0.875rem;
}

.input {
	font-size: 0.875rem;
	font-weight: 400;
	color: var(--color3);
	height: 48px;
	background-color: rgba(0, 0, 0, 0.04);
	padding: 0 12px 0 12px;
	border-radius: 6px;
	outline: none;
	transition: 0.24s;
	&::placeholder{
		font-size: 0.875rem;
		color: #767676;
	}
	&:focus{
		box-shadow: 0px 0px 0px 2px var(--color1);
	}
	&:disabled,
	&.dispbled{
		pointer-events: none;
		color: #AAAAAA;
	}
	&.sm{
		height: 34px;
	}
	&.error{
		box-shadow: 0px 0px 0px 1.5px  #FA665B;
		+ .input-error{
			display: block;
		}
	}
	&.light{
		background-color: #FFFFFF;
		&::placeholder{
			color: #A8A8A8;
		}
	}
}

textarea.input {
	padding-top: 15px;
	line-height: 18px;
	min-height: 80px;
}


/*==========  Hover  ==========*/

@media (min-width: 1200px){
	.input:hover{
		background-color: rgba(0, 0, 0, 0.08);
	}
}

