
/*==========  BUTTONS  ==========*/

.btn {
	display: inline-block;
	vertical-align: bottom;
	text-align: center;
	position: relative;
	cursor: pointer;
	text-decoration: none;
	border: none;
	padding: 0;
	background: none;
	outline: none;
}

.btn-primary {
	position: relative;
	border-radius: 10px;
	padding: 8px 20px;
	font-size: 1rem;
	line-height: 1.4;
	font-weight: 700;
	background-color: var(--color2);
	color: var(--color3);
	border: 2px solid var(--color1);
	box-shadow: 0 3px 0 var(--color1);
	transition: var(--transition2);
	&:hover{
		transform: translateY(-3px);
		box-shadow: 0 5px 0 var(--color1);
	}
	&:active{
		transform: translateY(0px);
		box-shadow: 0 2px 0 var(--color1);
	}
}

.btn-secondary {
	border-radius: 16px;
	font-weight: 500;
}

.btn-link {
	font-weight: 700;
}

.btn-block {
	display: block;
	width: 100%;
}

.btn-close {
	width: 32px;
	height: 32px;
	font-size: 16px;
	font-weight: 800;
	line-height: 32px;
	background-color: rgba(0, 0, 0, 0.04);
	color: #323232;
	position: relative;
	z-index: 1;
	cursor: pointer;
	outline: none;
	display: block;
	text-align: center;
	border-radius: 50%;
	transition: all .37s ease-out;
	&:after{
		content: "\02715";
	}
}

.btn-wrapper{
	display: flex;
	align-items: center;
	&.left{
		justify-content: flex-start;
	}
	&.right{
		justify-content: flex-end;
	}
	&.center{
		justify-content: center;
	}
	> *{
		margin-right: 15px;
		&:last-child{
			margin-right: 0;
		}
	}
	&.sm{
		> *{
			margin-right: 5px;
			&:last-child{
				margin-right: 0;
			}
		}
	}
}

