/*-------------------------------------------------------------------------------------------------------------------------------*/
/* Section settings */
/*-------------------------------------------------------------------------------------------------------------------------------*/
.section {
	position: relative;
	z-index: 0;
}
.container-cs{
	max-width: 1200px;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
}
.row{
	&.row-5{
		margin-right: -5px;
		margin-left: -5px;
		> [class*=col]{
			padding-right: 5px;
			padding-left: 5px;
		}
	}
	&.row-40{
		margin-right: -20px;
		margin-left: -20px;
		> [class*=col]{
			padding-right: 20px;
			padding-left: 20px;
		}
	}
	&.row-100{
		margin-right: -50px;
		margin-left: -50px;
		> [class*=col]{
			padding-right: 50px;
			padding-left: 50px;
		}
	}
}

