/*==========  Footer Style  ==========*/
.footer{
  background-color: var(--color2);
  &__inner{
    padding-top: 50px;
    padding-bottom: 50px;
  }
  &__tp{
    margin-bottom: 50px;
  }
  &__block{
    .title{
      font-size: 1.1rem;
      color: var(--color3);
      font-weight: 500;
      margin-bottom: 12px;
    }
    .menu{
      li{
        margin-bottom: 8px;
        line-height: 1em;
        &:last-child{
          margin-bottom: 0;
        }
        a{
          font-size: 0.9rem;
          letter-spacing: 0.04em;
          transition: var(--base-transition);
          text-decoration: none;
          color: var(--color3);
          &:hover{
            opacity: 0.8;
          }
          &.active{
            pointer-events: none;
            opacity: 0.8;
          }
        }
      }
      &.type2{
        li{
          display: inline-block;
          margin-bottom: 8px;
          margin-right: 8px;
        }
      }
    }
  }
}
