@media (max-width: 991px){
  .footer{
    &__inner{
      padding-bottom: 10px;
      padding-top: 30px;
    }
    &__tp{
      margin-bottom: 10px;
    }
    &__block {
      margin-bottom: 20px;
      .title {
        margin-bottom: 8px;
        text-align: center;
      }
      .menu{
        text-align: center;
      }
    }
  }
}

