/*-------------------------------------------------------------------------------------------------------------------------------*/
/* Section settings */
/*-------------------------------------------------------------------------------------------------------------------------------*/
.section {
  position: relative;
  z-index: 0;
}

.container-cs {
  max-width: 1200px;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

.row.row-5 {
  margin-right: -5px;
  margin-left: -5px;
}

.row.row-5 > [class*=col] {
  padding-right: 5px;
  padding-left: 5px;
}

.row.row-40 {
  margin-right: -20px;
  margin-left: -20px;
}

.row.row-40 > [class*=col] {
  padding-right: 20px;
  padding-left: 20px;
}

.row.row-100 {
  margin-right: -50px;
  margin-left: -50px;
}

.row.row-100 > [class*=col] {
  padding-right: 50px;
  padding-left: 50px;
}

/*==========  MEDIA QUERIES  ==========*/
/* BG */
.video-bg:after,
.bg:after {
  content: "";
  display: block;
}

.video-bg,
.video-bg:after,
.wr-hd,
.bg,
.bg:after {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.bg {
  background-size: cover;
  background-repeat: no-repeat;
}

.wr-hd {
  overflow: hidden;
  z-index: -1;
}

.video-bg video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%) scale(1.1);
}

/* Icon arrow */
.icon-arrow {
  position: relative;
  display: inline-block;
  width: 7px;
  height: 7px;
}

.icon-arrow:before, .icon-arrow:after {
  content: "";
  position: absolute;
  top: 0;
  width: 1px;
  height: 9px;
  background-color: #111111;
}

.icon-arrow:before {
  left: 0;
  transform: rotate(-45deg);
}

.icon-arrow:after {
  right: 0;
  transform: rotate(45deg);
}

.image-wrapper img {
  max-width: 100%;
}

/*==========  GLOBAL  ==========*/
body {
  font-family: var(--font-family);
  color: var(--color3);
}

b, strong {
  font-weight: 700;
}

a {
  transition: var(--base-transition);
}

a:hover {
  text-decoration: none;
}

ul, ol {
  list-style: none;
}

/*==========  SIZE  ==========*/
/* Text size */
html {
  font-size: 16px;
}

body, .text {
  font-size: 1rem;
  line-height: 1.65em;
  font-weight: var(--regular);
}

.text-md {
  line-height: 1.3em;
  font-size: 16px;
}

.text-sm {
  line-height: 1.3em;
  font-size: 14px;
}

.text-lg {
  line-height: 1.4em;
  font-size: 20px;
}

.text-xl {
  line-height: 1.3em;
  font-size: 24px;
}

h1, .h1 {
  line-height: 1.2em;
  font-size: 3rem;
}

h2, .h2 {
  line-height: 1.2em;
  font-size: 1.8rem;
}

h3, .h3 {
  line-height: 1.2em;
  font-size: 1.4rem;
}

h4, .h4 {
  line-height: 1.2em;
  font-size: 1.2rem;
}

h5, .h5 {
  line-height: 1.4em;
  font-size: 1.1rem;
}

h6, .h6 {
  line-height: 1.4em;
  font-size: 1rem;
}

/*==========  MEDIA QUERIES  ==========*/
@media (max-width: 991px) {
  html {
    font-size: 14px;
  }
}
/*==========  SIMPLE CONTENT  ==========*/
.text > * {
  margin-bottom: 16px;
}

.text > *:last-child {
  margin-bottom: 0;
}

.text img {
  max-width: 100%;
  height: auto;
  display: block;
}

.text ol {
  counter-reset: number;
}

.text ol li {
  padding-left: 0;
  margin-bottom: 15px;
  position: relative;
}

.text ol li:before {
  counter-increment: number;
  content: "0" counter(number) ".";
  margin-right: 8px;
  font-weight: 700;
  font-size: 14px;
  color: var(--dark);
}

.text ol li:last-of-type {
  margin-bottom: 0;
}

.text ul li {
  padding-left: 20px;
  margin-bottom: 2px;
  position: relative;
}

.text ul li:before {
  content: "";
  display: inline-block;
  background-color: #111111;
  background-repeat: no-repeat;
  background-size: contain;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  vertical-align: middle;
  position: absolute;
  left: 5px;
  top: 0.55em;
}

.text ul li:last-of-type {
  margin-bottom: 0;
}

.text blockquote {
  color: var(--dark);
  font-size: 20px;
  padding: 5px 5px 5px 25px;
  border-left: 4px solid var(--dark);
  font-weight: 600;
}

.text > iframe {
  width: 100%;
  height: 480px;
  max-height: calc(56.3vw - 30px);
}

.text table {
  border-left: 1px solid var(--dark);
  border-top: 1px solid var(--dark);
  border-spacing: 0;
  text-align: left;
}

.text table th,
.text table td {
  padding: 8px;
  border-right: 1px solid var(--dark);
  border-bottom: 1px solid var(--dark);
}

.text b {
  font-weight: var(--bold);
}

.text strong {
  font-weight: var(--bold);
}

.text a {
  text-decoration: none;
  color: var(--dark);
}

/*==========  Hover  ==========*/
@media (min-width: 1200px) {
  .text a:hover {
    opacity: 0.85;
  }
}
/*==========  MEDIA QUERIES  ==========*/
@media (max-width: 991px) {
  .text > * {
    margin-bottom: 8px;
  }
  .text > *:last-child {
    margin-bottom: 0;
  }
}
/*==========  TYPOGRAPHY  ==========*/
/* Align */
.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

/* Text weight */
.fw-light {
  font-weight: var(--light);
}

.fw-regular {
  font-weight: var(--regular);
}

.fw-medium {
  font-weight: var(--medium);
}

.fw-bold {
  font-weight: var(--bold);
}

/* Text color */
.color-white {
  color: var(--white);
}

.color-dark {
  color: var(--dark);
}

/*==========  Hover  ==========*/
/*==========  MEDIA QUERIES  ==========*/
/*==========  BUTTONS  ==========*/
.btn {
  display: inline-block;
  vertical-align: bottom;
  text-align: center;
  position: relative;
  cursor: pointer;
  text-decoration: none;
  border: none;
  padding: 0;
  background: none;
  outline: none;
}

.btn-primary {
  position: relative;
  border-radius: 10px;
  padding: 8px 20px;
  font-size: 1rem;
  line-height: 1.4;
  font-weight: 700;
  background-color: var(--color2);
  color: var(--color3);
  border: 2px solid var(--color1);
  box-shadow: 0 3px 0 var(--color1);
  transition: var(--transition2);
}

.btn-primary:hover {
  transform: translateY(-3px);
  box-shadow: 0 5px 0 var(--color1);
}

.btn-primary:active {
  transform: translateY(0px);
  box-shadow: 0 2px 0 var(--color1);
}

.btn-secondary {
  border-radius: 16px;
  font-weight: 500;
}

.btn-link {
  font-weight: 700;
}

.btn-block {
  display: block;
  width: 100%;
}

.btn-close {
  width: 32px;
  height: 32px;
  font-size: 16px;
  font-weight: 800;
  line-height: 32px;
  background-color: rgba(0, 0, 0, 0.04);
  color: #323232;
  position: relative;
  z-index: 1;
  cursor: pointer;
  outline: none;
  display: block;
  text-align: center;
  border-radius: 50%;
  transition: all 0.37s ease-out;
}

.btn-close:after {
  content: "âœ•";
}

.btn-wrapper {
  display: flex;
  align-items: center;
}

.btn-wrapper.left {
  justify-content: flex-start;
}

.btn-wrapper.right {
  justify-content: flex-end;
}

.btn-wrapper.center {
  justify-content: center;
}

.btn-wrapper > * {
  margin-right: 15px;
}

.btn-wrapper > *:last-child {
  margin-right: 0;
}

.btn-wrapper.sm > * {
  margin-right: 5px;
}

.btn-wrapper.sm > *:last-child {
  margin-right: 0;
}

/*==========  Hover  ==========*/
/*==========  MEDIA QUERIES  ==========*/
/*==========  Header Style  ==========*/
.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  background-color: var(--white);
  border-bottom: 2px solid var(--color2);
}

.header__inner {
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__inner .logo {
  margin-right: auto;
  line-height: 0;
  width: 35px;
}

.header__inner .logo img {
  max-width: 100%;
}

.header__inner .nav {
  margin: 0 -12px;
}

.header__inner .menu > li {
  display: inline-block;
  margin-right: 30px;
  position: relative;
}

.header__inner .menu > li a {
  letter-spacing: 0.04em;
  transition: var(--base-transition);
  display: block;
  border-radius: 4px;
  padding: 4px 12px;
  text-decoration: none;
  color: var(--color3);
}

.header__inner .menu > li a:hover {
  background-color: var(--color2);
}

.header__inner .menu > li a.active {
  pointer-events: none;
  background-color: var(--color2);
}

.header__inner .menu > li:hover .sub-menu {
  opacity: 1 !important;
  visibility: visible !important;
  transition-delay: 0s;
  transition-duration: 0.34s;
  transition-timing-function: ease-out;
  transform: translate(-50%, 0) !important;
}

.header__inner .sub-menu {
  position: absolute;
  top: calc(100% + 10px);
  left: 50%;
  transform: translate(-50%, 10px);
  padding: 10px;
  border: 2px solid var(--color2);
  border-radius: 4px;
  background-color: var(--white);
  transition-delay: 0.4s;
  transition-duration: 0.34s;
  transition-timing-function: ease-out;
  opacity: 0;
  visibility: hidden;
}

.header__inner .sub-menu:after {
  content: "";
  display: block;
  width: 10px;
  height: 10px;
  background-color: var(--white);
  border-left: 2px solid var(--color2);
  border-top: 2px solid var(--color2);
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -10px;
  transform: rotate(45deg) translateY(-10px);
}

.header__inner .sub-menu > li {
  display: block;
  margin-bottom: 2px;
}

.header__inner .sub-menu > li > a {
  font-size: 0.9rem;
  white-space: nowrap;
  padding: 2px 10px;
}

.header__burger {
  width: 22px;
  flex: 0 0 22px;
  height: 16px;
  position: relative;
  cursor: pointer;
  z-index: 4;
  margin-left: auto;
  display: none;
}

.header__burger .close,
.header__burger .open {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.header__burger .close span,
.header__burger .open span {
  width: 22px;
  height: 2px;
  display: block;
  position: absolute;
  background-color: var(--color1);
  border-radius: 30px;
  backface-visibility: hidden;
  transition: width 0.32s;
}

.header__burger .open span:nth-child(1) {
  top: 0;
  left: 0;
  transition-delay: 0.5s;
}

.header__burger .open span:nth-child(2) {
  top: 7px;
  right: 0;
  transition-delay: 0.55s;
}

.header__burger .open span:nth-child(3) {
  bottom: 0;
  left: 0;
  transition-delay: 0.6s;
}

.header__burger .close span {
  width: 0;
}

.header__burger .close span:nth-child(1) {
  top: 0px;
  left: 3px;
  transform: rotate(45deg);
  transform-origin: left;
  transition-delay: 0s;
}

.header__burger .close span:nth-child(2) {
  top: 0px;
  right: 4px;
  transform: rotate(-45deg);
  transform-origin: right;
  transition-delay: 0.08s;
}

.header.active-menu:after {
  border-bottom-color: rgba(1, 1, 1, 0);
}

.header.active-menu .header__burger .close span {
  width: 22px;
}

.header.active-menu .header__burger .close span:nth-child(1) {
  transition-delay: 0.5s;
}

.header.active-menu .header__burger .close span:nth-child(2) {
  transition-delay: 0.58s;
}

.header.active-menu .header__burger .open span {
  width: 0;
}

.header.active-menu .header__burger .open span:nth-child(1) {
  transition-delay: 0s;
}

.header.active-menu .header__burger .open span:nth-child(2) {
  transition-delay: 0.05s;
}

.header.active-menu .header__burger .open span:nth-child(3) {
  transition-delay: 0.1s;
}

