.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 22;
  background-color: rgba(0, 0, 0, 0.4);
  transition: all 0.22s ease-out;
  opacity: 0;
  visibility: hidden;
}

.popup-conteiner {
  display: block;
  width: 100%;
  position: fixed;
  top: 100%;
  left: 50%;
  transform: translate(-50%, 0);
  background: #fff;
  border-radius: 16px;
  z-index: 24;
  opacity: 0;
  visibility: hidden;
  transition: all 0.24s ease-out;
}

