/*==========  TYPOGRAPHY  ==========*/


/* Align */
.text-left {
	text-align: left;
}

.text-center {
	text-align: center;
}

.text-right {
	text-align: right;
}

/* Text weight */
.fw-light {
	font-weight: var(--light);
}
.fw-regular {
	font-weight: var(--regular);
}
.fw-medium {
	font-weight: var(--medium);
}
.fw-bold{
	font-weight: var(--bold);
}

/* Text color */
.color-white {
	color: var(--white);
}
.color-dark {
	color: var(--dark);
}


