/*-------------------------------------------------------------------------------------------------------------------------------*/
/* GLOBAL SETTINGS */
/*-------------------------------------------------------------------------------------------------------------------------------*/

html {
	height: 100%;
}

body {
	height: 100%;
}

#content-block {
	overflow: hidden;
	position: relative;
	&.header-sp{
		padding-top: 60px;
	}
}

.overflow-hidden {
	overflow: hidden;
}

html.overflow-hidden body {
	overflow: hidden;
}

/*==========  MEDIA QUERIES  ==========*/

// @media (max-width: 1368px)

// @media (max-width: 1199px){}

@media (max-width: 991px){
	#content-block {
		&.header-sp{
			padding-top: 50px;
		}
	}
}
