/*==========  Header Style  ==========*/
.header{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 99;
  background-color: var(--white);
  border-bottom: 2px solid var(--color2);
  &__inner{
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .logo{
      margin-right: auto;
      line-height: 0;
      width: 35px;
      img{
        max-width: 100%;
      }
    }
    .nav{
      margin: 0 -12px;
    }
    .menu{
      > li{
        display: inline-block;
        margin-right: 30px;
        position: relative;
        a{
          letter-spacing: 0.04em;
          transition: var(--base-transition);
          display: block;
          border-radius: 4px;
          padding: 4px 12px;
          text-decoration: none;
          color: var(--color3);
          &:hover{
            background-color: var(--color2);
          }
          &.active{
            pointer-events: none;
            background-color: var(--color2);
          }
        }
        &:hover{
          .sub-menu{
            opacity: 1!important;
            visibility: visible!important;
            transition-delay: 0s;
            transition-duration: .34s;
            transition-timing-function: ease-out;
            transform: translate(-50%, 0)!important;
          }
        }
      }
    }
    .sub-menu{
      position: absolute;
      top: calc(100% + 10px);
      left: 50%;
      transform: translate(-50%, 10px);
      padding: 10px;
      border: 2px solid var(--color2);
      border-radius: 4px;
      background-color: var(--white);
      transition-delay: 0.4s;
      transition-duration: .34s;
      transition-timing-function: ease-out;
      opacity: 0;
      visibility: hidden;
      &:after{
        content: '';
        display: block;
        width: 10px;
        height: 10px;
        background-color: var(--white);
        border-left: 2px solid var(--color2);
        border-top: 2px solid var(--color2);
        position: absolute;
        top: 0;
        left: 50%;
        margin-left: -10px;
        transform: rotate(45deg) translateY(-10px);
      }
      > li{
        display: block;
        margin-bottom: 2px;
        > a{
          font-size: 0.9rem;
          white-space: nowrap;
          padding: 2px 10px;
        }
      }
    }
  }
  &__burger{
    width: 22px;
    flex: 0 0 22px;
    height: 16px;
    position: relative;
    cursor: pointer;
    z-index: 4;
    margin-left: auto;
    display: none;
    .close,
    .open{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      span{
        width: 22px;
        height: 2px;
        display: block;
        position: absolute;
        background-color: var(--color1);
        border-radius: 30px;
        backface-visibility: hidden;
        transition: width 0.32s;
      }
    }
    .open{
      span{
        &:nth-child(1){
          top: 0;
          left: 0;
          transition-delay: 0.5s;
        }
        &:nth-child(2){
          top: 7px;
          right: 0;
          transition-delay: 0.55s;
        }
        &:nth-child(3){
          bottom: 0;
          left: 0;
          transition-delay: 0.6s;
        }
      }
    }
    .close{
      span{
        width: 0;
        &:nth-child(1){
          top: 0px;
          left: 3px;
          transform: rotate(45deg);
          transform-origin: left;
          transition-delay: 0s;
        }
        &:nth-child(2){
          top: 0px;
          right: 4px;
          transform: rotate(-45deg);
          transform-origin: right;
          transition-delay: 0.08s;
        }
      }
    }
  }
  &.active-menu{
    &:after{
      border-bottom-color: rgba(1,1,1,0);
    }
    .header__burger{
      .close{
        span{
          width: 22px;
          &:nth-child(1){
            transition-delay: 0.5s;
          }
          &:nth-child(2){
            transition-delay: 0.58s;
          }
        }
      }
      .open{
        span{
          width: 0;
          &:nth-child(1){
            transition-delay: 0s;
          }
          &:nth-child(2){
            transition-delay: 0.05s;
          }
          &:nth-child(3){
            transition-delay: 0.1s;
          }
        }
      }
    }
  }
}

