/*==========  MEDIA QUERIES  ==========*/

// @media (max-width: 1368px){}

// @media (max-width: 1199px){}

@media (min-width: 991px) {
  .header__inner .nav .menu>li .sub-menu{
    display: block!important;
  }
}

@media (max-width: 991px){
  .header{
    &__inner{
      height: 50px;
      .logo {
        width: 28px;
      }
      .nav{
        position: absolute;
        left: 0;
        right: 0;
        top: calc(100% + 2px);
        background-color: var(--white);
        transition: var(--base-transition);
        visibility: hidden;
        opacity: 0;
        margin: 0;
        .menu{
          >li {
            display: block;
            margin-right: 0;
            position: relative;
            border-bottom: 1px solid var(--color2);
            a{
              background-color: var(--white)!important;
            }
            > a {
              position: relative;
              z-index: 2;
              &:after{
                content: "";
                background-color: var(--white);
                border-left: 2px solid var(--color2);
                border-top: 2px solid var(--color2);
                display: block;
                height: 10px;
                position: absolute;
                width: 10px;
                left: auto;
                right: 15px;
                top: 12px;
                margin-left: 0;
                transition: var(--base-transition);
                transform: rotate(45deg);
              }
            }
            .sub-menu{
              position: static!important;
              transform: none!important;
              visibility: visible!important;
              opacity: 1!important;
              border-width: 1px;
              border-radius: 0;
              border-bottom: none;
              transition: none!important;
              display: none;
              &:after{
                display: none;
              }
              li{
                margin-bottom: 0;
              }
            }
            &.open{
              > a{
                &:after{
                  transform: rotate(-135deg);
                }
              }
            }
          }
        }
      }
    }
    &.active-menu{
      .nav{
        visibility: visible;
        opacity: 1;
      }
    }
    &__burger{
      display: block;
    }
  }
}

